import React, { useState, useEffect, useRef, useReducer } from "react";
import { data } from "./data";
import Styles from "./styles";

function reduser(state , action){
  if(action.type === "DARK"){
    return Styles.darkMode.dark
  }
  else{
    return Styles.darkMode.light
  }
}

const __init__ = Styles.screenStyle;
const modalMessage = {
  message : "Please check all the dependencies!",
  show : false
}

function modal(state , action){
    if(action.type === "ADD"){
      return {...modalMessage , show : true}
    }
    else if(action.type === "READD"){
      return {...modalMessage , show : false}
    }
}



function Listapp() {
  const [users, setdata] = useState(data);
  const [addstyle, addpage] = useState("none");
  const [mainstyle, mainpage] = useState("block");
  const [editstyle, editpage] = useState("none");
  const [uedity, edity] = useState("");
  const [dark , setDark] = useState(true)
  const inputref = useRef(null);
  const addref = useRef(null);
  const [state , dispatch] = useReducer(reduser , __init__)
  const [modalstate , modaldis] = useReducer(modal , modalMessage)

  function Modal(probs){
    return(
        <div className="modal" style={{color : state.Mfg , backgroundColor : state.Mbg , border : state.Border}}>
            <h1>{probs.Message}</h1>
        </div>
    )
}

  function darkMode(){
    if(dark){
      dispatch({type : "DARK"})
    }
    else{
      dispatch({type : "LIGHT"})
    }
    setDark(!dark)
  }

 

  useEffect(() => {
    inputref.current.focus();
    addref.current.focus();
  });

  function add() {
    mainpage("none");
    addpage("block");
  }

  function cancel(which) {
    var ok = window.confirm("Are you sure?");
    if(which === editstyle && ok){
    editpage("none");
    mainpage( "block");
    document.getElementById("e_name").value = "";
    document.getElementById("e_last_name").value = "";
    }
    else if(which === addstyle && ok){
      document.getElementById("name").value = "";
      document.getElementById("last_name").value = "";
      addpage("none");
      mainpage("block");
    }
    
  }


  function save(which){
    if(which === "add"){
      if(document.getElementById("name").value && document.getElementById("last_name").value){
      var i = users.pop();
      users.push(i);
      var newlastname = document.getElementById("last_name").value;
      var newname = document.getElementById("name").value;
      var newuser = { id: i.id + 1, name: newname, last_name: newlastname };
      setdata([...users, newuser]);
      document.getElementById("name").value = "";
      document.getElementById("last_name").value = "";
      addpage("none");
      mainpage("block");}
      else{
        modaldis({type : "ADD"})
        setTimeout(() => modaldis({type : "READD"}),1000)
      }
    }
    else if(which === "edit"){
      if(document.getElementById("e_name").value && document.getElementById("e_last_name").value){
      for (var contact of users) {
        if (contact.id === uedity.id) {
          var ind = users.indexOf(contact);
          var edited_name = document.getElementById("e_name").value;
          var edited_lastname = document.getElementById("e_last_name").value;
          var newcontact = {
            id: uedity.id,
            name: edited_name,
            last_name: edited_lastname,
          };
          var newusers = users;
          newusers[ind] = newcontact;
          setdata([...newusers]);

          editpage("none");
          mainpage("block");}
      }
    }
    else{
        modaldis({type : "ADD"})
        setTimeout(() => modaldis({type : "READD"}),1000)
      }
    }
  }


  
  function Editcontact(edit_usr) {
    edity(edit_usr);
    mainpage("none");
    editpage("block");
    var name_inp = document.getElementById("e_name");
    var last_name_inp = document.getElementById("e_last_name");
    name_inp.value = edit_usr.name;
    last_name_inp.value = edit_usr.last_name;
  }

  function Deletecontact(del_id) {
    var ok = window.confirm("Are you sure you want to do that?");
    if (ok) {
      setdata(users.filter((user) => user.id !== del_id));
    }
  }

  return (
    <div className="container">
      <div className="listapp" style={({},{display : mainstyle,backgroundColor :  state.Bg})}>
        <h1 className="header" style={{} , {color : state.Fg}}>Contacts</h1>
        <div className="second_frame">
          {users.map((user) => {
            return (
              <section className="contact" key={user.id} style={{} , {color : state.Fg , backgroundColor : state.contactBg , border : state.Border}}>
                <div className="c_names">
                  <h1>{user.id}.</h1>
                  <h2 className="cname">{user.name}</h2>
                  <h2 className="clast_name">{user.last_name}</h2>
                </div>
                <div className="c_buttons">
                  <button className="btn" onClick={() => Editcontact(user)} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
                    Edit
                  </button>
                  <button
                    className="btn"
                    onClick={() => Deletecontact(user.id)}
                    style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg ,border : state.Border}}
                  >
                    Delete
                  </button>
                </div>
              </section>
            );
          })}
        </div>
        <button className="btn" onClick={add} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
          {" "}
          Add
        </button>
  <label className="switch">
    <input type="checkbox" onClick={darkMode}/>
    <span className="slider round"></span>
  </label>
      </div>

      <div className="add" style={({},{display : addstyle,backgroundColor : state.Bg})}>
        <section>
          {modalstate.show && <Modal Message={modalstate.message}/>}
          <label style={{} , {color : state.Fg}}>Name:</label>
          <input type="text" className="inp" id="name" ref={addref} />
        </section>
        <section>
          <label style={{} , {color : state.Fg}}> Last name:</label>
          <input type="text" className="inp" id="last_name" />
        </section>
        <button type="button" className="btn" onClick={() => cancel(addstyle)} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
          Cancel
        </button>
        <button type="button" className="btn" onClick={() => save("add")} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
          Save
        </button>
      </div>

      <div className="add" style={({},{display : editstyle,backgroundColor : state.Bg})}>
        <section>
          {modalstate.show && <Modal Message={modalstate.message}/>}
          <label style={{} , {color : state.Fg}}>Name:</label>
          <input type="text" className="inp" id="e_name" ref={inputref} />
        </section>
        <section>
          <label style={{} , {color : state.Fg}}>Last name:</label>
          <input type="text" className="inp" id="e_last_name" />
        </section>
        <button type="button" className="btn" onClick={() => cancel(editstyle)} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
          Cancel
        </button>
        <button type="button" className="btn" onClick={() => save("edit")} style={{} , {backgroundColor : state.buttonBg , color : state.buttonFg , border : state.Border}}>
          Save
        </button>
      </div>
    </div>
  );
}

export default Listapp;
